@use "../utils" as *;

.button-round {
    @include reset-button;
    @include reset-link;
    position: relative;
    width: 58px;
    height: 58px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--color-grey);
    border-radius: var(--border-radius-round);
    @include tooltip;

    &:active,
    &:hover {
        background-color: var(--color-grey-light);
    }

    &__icon {
        font-size: var(--font-size-30);
    }
}