@use "../utils" as *;

.editor {
    display: flex;
    flex-direction: column;
    gap: var(--gap-30);

    &__content {
        display: flex;
        flex-direction: column;
        gap: var(--gap-30);
    }

    &__input-list {
        display: grid;
        align-items: center;
        gap: var(--gap-10);
    }

    &__sets {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: var(--gap-10);
    }

    &__set {
        @include reset-button;
        position: relative;
        width: 100%;
        display: flex;
        align-items: center;
        gap: var(--gap-10);
        padding: var(--padding-10);
        background-color: var(--color-grey);
        border-radius: var(--border-radius-5);
        @include tooltip;

        &:active,
        &:hover {
            background-color: var(--color-grey-light);
        }
    }

    &__set-id,
    &__set-name,
    &__set-cases {
        padding: var(--padding-5) var(--padding-10);
        background-color: var(--color-grey-dark);
        border-radius: var(--border-radius-5);
    }

    &__set-cases {
        margin-left: auto;
    }

    @media (min-width: $breakpoint-l) {
        &__input-list {
            grid-template-columns: 1fr 1fr;
        }
    }
}