:root {
    // Main colors
    --color-blue: #0070F0;
    --color-blue-light: #33A9FF;
    --color-grey-very-dark: #181818;
    --color-grey-dark: #222222;
    --color-grey: #444444;
    --color-grey-light: #666666;
    --color-grey-very-light: #888888;
    --color-grey-super-light: #BBBBBB;
    --color-green: #15BD4A;
    --color-green-light: #48F07D;
    --color-orange: #FF7800;
    --color-orange-light: #FFAB33;
    --color-purple: #B911B9;
    --color-red: #E01B4C;
    --color-red-light: #FF4E7F;
    --color-white: #FFFFFF;
    --color-yellow: #FFE609;
    --color-yellow-light: #FFFF3C;

    --color-ignore: var(--color-grey);

    // Brand colors
    --color-discord: #5865F2;

    // Layout
    --gap-5: 5px;
    --gap-10: 10px;
    --gap-15: 15px;
    --gap-20: 20px;
    --gap-30: 30px;
    --gap-40: 40px;
    --gap-50: 50px;

    --margin-10: 10px;
    --margin-20: 20px;

    --padding-2: 2px;
    --padding-5: 5px;
    --padding-8: 8px;
    --padding-10: 10px;
    --padding-15: 15px;
    --padding-20: 20px;
    --padding-30: 30px;

    --max-width-page: 1440px;

    --z-index-modal: 40;
    --z-index-timer: 30;
    --z-index-tooltip: 20;
    --z-index-controls: 10;
    --z-index-above: 1;
    --z-index-base: 0;
    --z-index-below: -1;

    // Fonts
    --font-family-text: 'Roboto', sans-serif;

    --font-size-reference: 16; // Used to convert px values to rem

    --font-size-14: calc((14 / var(--font-size-reference)) * 1rem);
    --font-size-16: calc((16 / var(--font-size-reference)) * 1rem);
    --font-size-18: calc((18 / var(--font-size-reference)) * 1rem);
    --font-size-20: calc((20 / var(--font-size-reference)) * 1rem);
    --font-size-24: calc((24 / var(--font-size-reference)) * 1rem);
    --font-size-30: calc((30 / var(--font-size-reference)) * 1rem);
    --font-size-36: calc((36 / var(--font-size-reference)) * 1rem);
    --font-size-46: calc((46 / var(--font-size-reference)) * 1rem);
    --font-size-60: calc((60 / var(--font-size-reference)) * 1rem);

    --font-size-default: var(--font-size-16);

    --font-weight-regular: 400;
    --font-weight-medium: 500;

    --line-height-100: 100%;
    --line-height-150: 150%;

    --line-height-default: var(--line-height-150);

    --letter-spacing-3: 3px;

    // Borders
    --border-width-2: 2px;
    --border-width-3: 3px;
    --border-width-5: 5px;

    --border-radius-2: 2px;
    --border-radius-5: 5px;
    --border-radius-10: 10px;
    --border-radius-round: 50%;

    // Opacities
    --opacity-0: 0;
    --opacity-0-25: 0.15;
    --opacity-0-9: 0.9;
    --opacity-1: 1;

    // Transition
    --transition-duration-0-15: 0.15s;

    --transition-function-ease: ease;

    --transition-default: var(--transition-duration-0-15) var(--transition-function-ease);
}