@use "../utils" as *;

.editor-case {
    display: flex;
    flex-direction: column;
    gap: var(--gap-30);

    &__input-list {
        display: grid;
        align-items: center;
        gap: var(--gap-10);
    }

    &__columns {
        display: grid;
        gap: var(--gap-30);
    }

    &__column {
        display: flex;
        flex-direction: column;
        gap: var(--gap-30);
    }

    &__puzzle-preview {
        width: 100%;
        max-width: 300px;
        aspect-ratio: 1;
    }

    &__solutions {
        display: flex;
        flex-direction: column;
        gap: var(--gap-20);
    }

    &__solution {
        display: flex;
        flex-direction: column;
        gap: var(--gap-20);
    }

    @media (min-width: $breakpoint-l) {
        &__input-list {
            grid-template-columns: 1fr 4fr 1fr;
        }

        &__columns {
            grid-template-columns: 1fr 2fr;
        }
    }
}