@use "../utils" as *;

.case {
    $block-context: &;

    display: flex;
    flex-direction: column;
    gap: var(--gap-20);

    &__puzzle-preview {
        width: 100%;
        max-width: 200px;
        display: flex;
        align-self: center;
        opacity: var(--opacity-0-25);
    }

    &__content {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        gap: var(--gap-20);
    }

    &__info {
        display: flex;
        flex-direction: column;
        gap: var(--gap-20);
    }

    &__solutions {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: var(--gap-10);
    }

    &--selected {
        #{$block-context}__puzzle-preview {
            opacity: var(--opacity-1);
        }
    }

    @media (min-width: $breakpoint-l) {
        flex-direction: row;

        &__puzzle-preview {
            align-self: flex-start;
        }
    }

    @media (min-width: $breakpoint-xl) {
        &__info {
            flex-direction: row;
        }
    }
}