@font-face {
    font-family: 'Roboto';
    src: url("../../public/assets/fonts/roboto/Roboto-Regular.ttf") format('truetype');
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: 'Roboto';
    src: url("../../public/assets/fonts/roboto/Roboto-Medium.ttf") format('truetype');
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: "icons";
    src:
      url("../../public/assets/fonts/icons/icons.ttf") format("truetype"),
      url("../../public/assets/fonts/icons/icons.woff") format("woff");
    font-weight: normal;
    font-style: normal;
    font-display: block;
}