@use "../utils" as *;

.switch {
    $block-context: &;

    --width: 44px;
    --height: calc(var(--width) / 2);
    --padding: var(--padding-2);
    --size-toggle: calc(var(--height) - (var(--padding) * 2));
    --translate: calc(var(--width) / 2);

    display: flex;
    flex-direction: column;
    gap: var(--gap-5);

    &__description {
        line-height: var(--line-height-150)
    }

    &__field {
        position: relative;
        padding-left: calc(var(--width) + var(--padding-10));
    }

    &__input {
        @include hidden;

        &:focus {
            // Background
            ~ #{$block-context}__label::before {
                @include focus;
            }
        }

        &:checked {
            // Background
            ~ #{$block-context}__label::before {
                background-color: var(--color-green);
            }

            // Toggle
            ~ #{$block-context}__label::after {
                transform: translateX(var(--translate));
            }
        }
    }

    &__label {
        line-height: var(--line-height-150);
        color: var(--color-grey-super-light);
        cursor: pointer;

        // Background
        &::before {
            content: "";
            position: absolute;
            top: 2;
            left: 0;
            width: var(--width);
            height: var(--height);
            min-width: var(--width);
            background-color: var(--color-grey);
            border-radius: var(--border-radius-10);
            transition: background-color var(--transition-default);
        }

        // Toggle
        &::after {
            content: "";
            position: absolute;
            top: calc(var(--padding) + 2px);
            left: var(--padding);
            width: var(--size-toggle);
            height: var(--size-toggle);
            background-color: var(--color-white);
            border-radius: var(--border-radius-round);
            transition: transform var(--transition-default);
        }
    }
}