@use "../utils" as *;

.spoiler {
    $block-context: &;

    width: 100%;
    display: flex;
    justify-content: center;

    &__content {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: var(--gap-10);
    }

    &--alignment-left {
        #{$block-context}__content {
            align-items: flex-start;
        }
    }

    &--alignment-center {
        #{$block-context}__content {
            align-items: center;
        }
    }

    &--alignment-right {
        #{$block-context}__content {
            align-items: flex-end;
        }
    }
}