@use "../utils" as *;

.trainer {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: var(--gap-30);

    &__content {
        display: flex;
        flex-direction: column;
        gap: var(--gap-30);
    }

    &__columns {
        display: grid;
        gap: var(--gap-30);
    }

    &__column {
        display: flex;
        flex-direction: column;
        gap: var(--gap-30);
    }

    &__name {
        text-align: center;
        font-size: var(--font-size-20);
    }

    &__puzzle-preview {
        width: 100%;
        max-width: 200px;
        aspect-ratio: 1;
    }

    &__case-index {
        position: relative;
        font-size: var(--font-size-16);
        text-align: center;
        cursor: help;
        @include tooltip;
    }

    &__solutions {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: var(--gap-15);
    }

    &__times {
        display: flex;
        flex-direction: column;
        gap: var(--gap-30);
    }

    &__text {
        font-size: var(--font-size-16);
    }

    &__time-list {
        display: flex;
        flex-wrap: wrap;
        gap: var(--gap-10);
    }

    &__controls {
        z-index: var(--z-index-controls);
        position: fixed;
        right: 0;
        bottom: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: var(--padding-20);
        padding-right: var(--padding-20);
        padding-bottom: calc(var(--padding-30) + env(safe-area-inset-bottom));
        padding-left: var(--padding-20);
        gap: var(--gap-15);
    }

    &__controls-background {
        z-index: var(--z-index-below);
        position: absolute;
        inset: 0;
        background-color: var(--color-grey-dark);
        opacity: var(--opacity-0-9);
    }

    &__timer {
        @include reset-button;
        z-index: var(--z-index-timer);
        position: fixed;
        inset: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: var(--gap-50);
        padding: var(--padding-30);
        background-color: var(--color-grey-dark);
    }

    &__timer-value {
        padding: var(--padding-20);
        font-size: var(--font-size-36);
        color: var(--color-white);
        border: var(--border-width-5) solid var(--color-grey-light);
        border-radius: var(--border-radius-10);
    }

    &__timer-help {
        font-size: var(--font-size-24);
        text-align: center;
        color: var(--color-white);
    }

    @media (min-width: $breakpoint-xl) {
        &__columns {
            grid-template-columns: 1fr 2fr;
        }

        &__puzzle-preview {
            max-width: 300px;
        }

        &__case-index {
            font-size: var(--font-size-20);
        }

        &__timer-value {
            font-size: var(--font-size-60);
        }

        &__timer-help {
            font-size: var(--font-size-36);
        }
    }
}