@use "../utils" as *;

.cases {
    display: flex;
    flex-direction: column;
    gap: var(--gap-40);

    &__content {
        display: flex;
        flex-direction: column;
        gap: var(--gap-30);
    }

    &__accordions {
        display: flex;
        flex-direction: column;
        gap: var(--gap-10);
    }

    &__list {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: var(--gap-30);
    }

    &__item {
        display: flex;
        flex-direction: column;
        gap: var(--gap-30);
    }

    &__description {
        font-size: var(--font-size-18);
        line-height: var(--line-height-150);
    }
}