@use "../utils" as *;

.chart-line {
    width: 100%;

    &__zoom-container {
        position: relative;
        height: 300px;
    }

    &__chart-container {
        width: 100%;
        height: 100%;
        position: absolute;
        inset: 0;
        overflow-x: scroll;
        scrollbar-gutter: stable;
    }

    &__chart {
        min-width: 100%;
        height: 100%;
    }
}