@use "../utils" as *;

.list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    &__item--margin-left {
        margin-left: auto;
    }

    &--alignment-left {
        justify-content: flex-start;
    }

    &--alignment-center {
        justify-content: center;
    }

    &--alignment-right {
        justify-content: flex-end;
    }
}