@use "../utils" as *;

.accordion {
    $block-context: &;

    width: 100%;
    border: var(--border-width-3) solid var(--color-grey);
    border-radius: var(--border-radius-10);

    &__button {
        @include reset-button;
        position: relative;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: var(--gap-20);
        padding: var(--padding-20);
        @include tooltip;

        &:active,
        &:hover {
            background-color: var(--color-grey);
        }
    }

    &__title {
        font-size: var(--font-size-20);
        line-height: var(--line-height-150);
        text-align: left;
    }

    &__icon {
        font-size: var(--font-size-36);
        color: var(--color-white);
    }

    &__content {
        height: 0;
        overflow: hidden;
    }

    &__content-inner {
        display: flex;
        flex-direction: column;
        gap: var(--gap-20);
        padding: var(--padding-20);
    }

    &--alignment-left {
        #{$block-context}__content-inner {
            align-items: flex-start;
        }
    }

    &--alignment-center {
        #{$block-context}__content-inner {
            align-items: center;
        }
    }

    &--alignment-right {
        #{$block-context}__content-inner {
            align-items: flex-end;
        }
    }

    &--open {
        #{$block-context}__button {
            background-color: var(--color-grey);
        }

        #{$block-context}__content {
            height: initial;
            overflow: visible;
        }
    }

    @media (min-width: $breakpoint-l) {
        &__name {
            font-size: var(--font-size-24);
        }
    }
}