@use "../utils" as *;

.puzzle-preview {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    &__loader {
        width: 40%;
        height: 40%;
        margin: auto;
    }

    &__svg {
        width: 100%;
        aspect-ratio: 1;
    }

    &__error {
        container-type: size;
        position: relative;
        width: 40%;
        height: 40%;
        margin: auto;
        @include tooltip;
    }

    &__icon {
        font-size: 100cqb;
    }
}