@use "../utils" as *;

.editor-preview {
    display: flex;
    flex-direction: column;
    gap: var(--gap-30);

    &__puzzle-preview {
        position: relative;
        width: 100%;
        max-width: 450px;
        aspect-ratio: 1;
        cursor: pointer;
        @include tooltip;
    }

    &__list {
        display: flex;
        align-items: center;
        gap: var(--gap-20);
        flex-wrap: wrap;
    }

    &__color {
        @include reset-button;
        position: relative;
        width: 60px;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: var(--border-radius-10);
        @include tooltip;

        &[aria-selected='true'] {
           outline: var(--border-width-3) solid var(--color-white);
           outline-offset: 4px;
        }
    }
}