@use "../utils" as *;

.color-scheme-picker {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: var(--gap-10);

    &__list {
        display: flex;
        flex-wrap: wrap;
        gap: var(--gap-10);
    }

    &__item {
        position: relative;
        @include tooltip;
    }

    &__input {
        @include reset-input;
        width: 60px;
        height: 40px;
        border-radius: var(--border-radius-5);
        cursor: pointer;
    }
}