@use "../utils" as *;

.faq {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: var(--gap-30);

    &__content {
        display: flex;
        flex-direction: column;
        gap: var(--gap-30);
    }

    &__texts {
        display: flex;
        flex-direction: column;
        gap: var(--gap-5);
    }

    &__text {
        font-size: var(--font-size-16);
        line-height: var(--line-height-150);
        color: var(--color-white);
    }

    &__link {
        @include reset-link;
        color: var(--color-blue-light);

        &:active,
        &:hover {
            text-decoration: underline;
        }
    }
}