// General reset for input elements
@mixin reset-input {
    display: inline-block;
    padding: 0;
    margin: 0;
    vertical-align: middle;
    background: none;
    border: 0;
    border-radius: 0;
    box-shadow: none;
    appearance: none;
}

// General reset for link elements
@mixin reset-link {
    color: inherit;
    text-decoration: inherit;
    cursor: pointer;
}

// General reset for button elements
@mixin reset-button {
    padding: 0;
    font: inherit;
    line-height: inherit;
    color: inherit;
    text-transform: inherit;
    cursor: pointer;
    background-color: transparent;
    border: 0;
}

// Hide an element while keeping it accessible
@mixin hidden {
    position: absolute;
    width: 0;
    height: 0;
    margin: 0;
    padding: 0;
    border: 0;
    opacity: 0;
    overflow: hidden;
    pointer-events: none;
}

// Opposite of hidden
@mixin shown {
    position: inherit;
    width: initial;
    height: initial;
    margin: initial;
    padding: initial;
    border: initial;
    opacity: inherit;
    overflow: initial;
    pointer-events: all;
}

// Browser focus styles
@mixin focus {
    outline: 5px auto Highlight;
    outline: 5px auto -webkit-focus-ring-color;
}

// Show tooltip on hover and focus
@mixin tooltip {
    &[data-tooltip] {
        &:hover,
        &:focus {
            &::after {
                content: attr(data-tooltip);
                z-index: var(--z-index-tooltip);
                position: absolute;
                top: calc(100% + 5px);
                width: fit-content;
                padding: var(--padding-5);
                font-size: var(--font-size-14);
                line-height: var(--line-height-100);
                color: var(--color-white);
                white-space: nowrap;
                background-color: var(--color-grey-dark);
                border: var(--border-width-2) solid var(--color-grey);
                border-radius: var(--border-radius-5);
                pointer-events: none;
            }
        }

        // Align tooltip to the left
        &:not([data-tooltip-position]),
        &[data-tooltip-position="left"] {
            &:hover,
            &:focus {
                &::after {
                    left: 0;
                }
            }
        }

        // Align tooltip in the center
        &[data-tooltip-position="center"] {
            &:hover,
            &:focus {
                &::after {
                    left: 50%;
                    transform: translateX(-50%);
                }
            }
        }

        // Align tooltip to the right
        &[data-tooltip-position="right"] {
            &:hover,
            &:focus {
                &::after {
                    right: 0;
                }
            }
        }
    }
}