@use "../utils" as *;

.group {
    $block-context: &;

    position: relative;
    display: flex;
    flex-direction: column;
    gap: var(--gap-20);
    border: var(--border-width-3) solid var(--color-grey);
    border-radius: var(--border-radius-10);
    padding: var(--padding-20);

    &__title {
        z-index: var(--z-index-above);
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        width: fit-content;
        max-width: calc(100% - var(--margin-20));
        display: flex;
        align-items: center;
        margin-right: var(--margin-20);
        margin-left: var(--margin-20);
        transform: translateY(-60%);
        @include tooltip;

        &[data-tooltip] {
            cursor: help;
        }
    }

    &__title-text {
        padding-left: var(--padding-10);
        padding-right: var(--padding-10);
        font-size: var(--font-size-20);
        line-height: var(--line-height-150);
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        background-color: var(--color-grey-dark);
    }

    &__content {
        display: flex;
        flex-direction: column;
        gap: var(--gap-20)
    }

    &--alignment-left {
        #{$block-context}__content {
            align-items: flex-start;
        }
    }

    &--alignment-center {
        #{$block-context}__content {
            align-items: center;
        }
    }

    &--alignment-right {
        #{$block-context}__content {
            align-items: flex-end;
        }
    }

    &--full-width {
        width: 100%;
    }

    @media (min-width: $breakpoint-xl) {
        &__name {
            font-size: var(--font-size-24);
        }
    }
}