@use "../utils" as *;

.page-not-found {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--gap-30);

    &__logo {
        width: 50%;
        max-width: 200px;
        aspect-ratio: 1;
    }

    &__title {
        font-size: var(--font-size-24);
        text-align: center;
    }

    &__text {
        font-size: var(--font-size-18);
        text-align: center;
        line-height: var(--line-height-150);
    }

    @media (min-width: $breakpoint-l) {
        &__title {
            font-size: var(--font-size-36);
        }
    }
}